import { routerFilter } from '@/helpers/routes'
import landingPage from '@/views/home/LandingPage'

function loadHome(view) {
    return () => import( /* webpackChunkName: "home-view-[request]" */ `@/views/home/${view}.vue`)
}

const routes = [
    {
        path: '',
        name: 'LandingPage',
        meta: {
            requiresAuth: false,
            roleVerif: false,
            role: []
        },
        component: landingPage,
    },
    {
        path: '/:unit(paud|sd|smp|sma)',
        name: 'UnitProfile',
        meta: {
            requiresAuth: false,
            roleVerif: false,
            role: []
        },
        component: loadHome('UnitProfile'),
    },
    {
        path: '/ikatan-alumni-masehi-kudus',
        name: 'Ikamaku',
        meta: {
            requiresAuth: false,
            roleVerif: false,
            role: []
        },
        component: loadHome('Ikamaku'),
    },
    {
        path: '/kontak',
        name: 'Contact',
        meta: {
            requiresAuth: false,
            roleVerif: false,
            role: []
        },
        component: loadHome('Contact'),
    },
]

export default routerFilter(routes)