
import { 
    postData
} from '@/store/config/method'

const initialState = () => ({
    message: null,
    status: 0,
    user: {
        id: null,
        name: null,
        username: null,
        created_at: null,
    },
    token: null,
    schkey: null
})
const state = initialState
 
const getters = {
    getMessage: state => state.message,
    getStatus: state => state.status,
    getUser: state => state.user,
    getToken: state => state.token,
    getSchKey: state => state.schkey
}

function makeid() {
    let result           = '';
    const characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    for ( let i = 0; i < 20; i++ ) {
       result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
}

const actions = {
    async login({
        commit
    }, data) {
        try {
            const response = await postData('api_cms','/auth/login',data);
            commit('SET_LOGIN_DATA', response.data)
        } catch (_) {
            commit('SET_FAILED_LOGIN');
        }
    },
    async logoutUser({
        commit
    }, data) {
        try {
            const response = await postData('api_cms','/auth/logout',data);
            commit('SET_RESPONSE', response.data);
        } catch (err) {
            if(err.response){
                commit('SET_FAILED_MESSAGE', err.response);
            }else{
                commit('SET_FAILED')
            }
        }
    },
    async changePassword({
        commit
    }, data) {
        try {
            const response = await postData('api_cms','/auth/change-password',data);
            commit('SET_RESPONSE', response.data);
        } catch (err) {
            if(err.response){
                commit('SET_CHANGE_PASSWORD_RESPONSE', err.response);
            }else{
                commit('SET_FAILED')
            }
        }
    },
    resetState({commit}){
        commit('resetState')
    }
}

const mutations = {
    SET_FAILED(state) {
        state.message = 'Jaringan Bermasalah'
        state.status = 0
    },
   
    SET_FAILED_MESSAGE(state, error) {
        if(error.status == 400){
            state.message = 'Username atau Password Anda Salah'
            state.status = 0
        }else{
            state.message = 'Jaringan Bermasalah'
            state.status = 0
        }
    },
    SET_CHANGE_PASSWORD_RESPONSE(state, error) {
        if(error.status == 400){
            state.message = error.data.message
            state.status =  error.data.status
        }else{
            state.message = 'Jaringan Bermasalah'
            state.status = 0
        }
    },
    SET_FAILED_LOGIN(state) {
        state.message = 'Username atau Password Anda Salah'
        state.status = 0
    },
    SET_LOGIN_DATA(state, response){
        state.message = response.message
        state.status = response.status
        state.user = response.data.user;
        state.token = response.data.token
        const key = makeid()
        state.schkey = key
        localStorage.setItem('schkey',key)
    },
    resetState (state) {
        const initial = initialState()
        Object.keys(initial).forEach(key => { state[key] = initial[key] })
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}