import store from "@/store"
import localForage from 'localforage';
import { Trans } from './translation'

const routerFilter = async (routes) => {
  const temp = await localForage.getItem("auth")
  let myrole = null
  if(temp){
    myrole = temp.auth?.user?.role
  }
  const isLoggedIn = store.getters['getisLoggedIn']
  return routes.filter((row) => {
    return (row.meta.requiresAuth == isLoggedIn && ((row.meta.roleVerif && row.meta.role.some(role => role == myrole) || !row.meta.roleVerif))) || !row.meta.requiresAuth
  })
}
const routeMiddleware = async (to, from, next) =>{
  await store.restored
  if(to.query.lang){
      const lang = to.query.lang == 'id' ? 'id': 'en'
      await store.dispatch('locale/setCurrentLocale', lang)
  }else if(!store.getters['locale/getCurrentLocale']){
      await store.dispatch('locale/setCurrentLocale',  Trans.defaultLocale)
  }
  let locale = store.getters['locale/getCurrentLocale']
  if(!Trans.isLocaleSupported(locale)){
      locale = Trans.defaultLocale
  } 
  return Trans.changeLocale(locale).then(() => 
      validateUser(to, next)
  )
}
const validateUser = (to, next) => {
  if (to.path == '/logout' && !store.getters['getisLoggedIn']) {
      return next({
        name: 'Login'
      })
  }
  if (to.matched.some(route => route.meta.beforelogin) && store.getters['getisLoggedIn']) {
      return next({
          name: 'Dashboard'
      })
  }
  next()
}

export {
  routerFilter,
  routeMiddleware
}